@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");

:root {
  --font-base: "DM Sans", sans-serif;

  --primary-color: #edf2f8;
  --secondary-color: #071091;
  --secondary-blue-color: #034caf;
  --secondary-blue-light-color: #2977e6;
  --light-secondary-color: rgba(213, 214, 233, 0.5);
  --black-color: #030303;
  --lightGray-color: #e4e4e4;
  --gray-color: #6b7688;
  --brown-color: #46364a;
  --white-color: #ffffff;
  --typed-text-color: rgb(33, 2, 70);
  --heading-color: rgb(30, 3, 80);
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}



.btn{
  cursor: pointer;
  padding: 0.7rem 1.5rem;
  border: 0;  
}

h1{
  font-size: 2.5rem;

}

@media screen and (min-width: 768px) {
  h1{
    font-size: 2.8rem
  }
}

h2 {
  font-size: 1.8rem;

}

@media screen and (min-width: 768px) {
  h2 {
    font-size: 2.2rem
  }
}

p{
  font-size: 1.4rem;
  letter-spacing: 0.3px;
  font-weight: 500;
  
}

@media screen and (min-width: 768px) {
  p {
      font-size: 1.2rem;
    }
}

.container {
  max-width: 1100px;
  margin: 0 auto;
}

.app {
  background-color: var(--primary-color);
  font-family: var(--font-base);
}

.app__whitebg {
  background-color: var(--white-color);
}

.app__primarybg {
  background-color: var(--primary-color);
}

.app__container {
  width: 100%;
  min-height: 100vh;

  display: flex;
  flex-direction: row;
}

.app__flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.app__wrapper {
  flex: 1;
  width: 100%;
  flex-direction: column;
  /*  padding: 2rem 0 0 0; */
}

  @media screen and (max-width: 450px) {
    .app__wrapper{
      padding: 4rem 1rem 2rem;
    }
  }



.head-text {
  font-size: 2.75rem;
  font-weight: 800;
  text-align: center;
  color: var(--black-color);
  text-transform: capitalize;
}
    .head-text span {
    color: var(--secondary-color);
  }

  @media screen and (min-width: 2000px) {
    .head-text{
    font-size: 4rem;
  }
  }

  @media screen and (max-width: 450px) {
    .head-text{
    font-size: 2rem;
  }
}

.p-text {
  font-size: 0.8rem;
  text-align: left;
  /* color: var(--gray-color); */
  line-height: 1.5;
}
  @media screen and (min-width: 1024px) {
    .p-text{
      font-size: 1.75rem;
  }
}


.bold-text {
  font-size: 1rem;
  font-weight: 800;
  color: var(--black-color);
  text-align: left;
}
  @media screen and (min-width: 2000px) {
    .bold-text{
      font-size: 2rem;
  }
  }
  @media screen and (max-width: 450px) {
    .bold-text{
      font-size: 0.9rem;
  }
}

.app__social {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
}


.app__social div {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: var(--white-color);
    margin: 0.25rem 0;
    border: 1px solid var(--lightGray-color);

    display: flex;
    justify-content: center;
    align-items: center;

    transition: all 0.3s ease-in-out;
}
.app__social svg {
      width: 15px;
      height: 15px;
      color: var(--gray-color);
    }

  .app__social:hover {
      background-color: var(--secondary-color);
      border-color: var(--secondary-color);
  }
         .app__social svg {
        color: var(--white-color);
      }
    

    @media screen and (min-width: 2000px) {
    .app__social{
      width: 70px;
      height: 70px;
      margin: 0.5rem 0;
    }
         .app__social svg {
        width: 30px;
        height: 30px;
      }
    }
  


.app__navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
}

  .app__navigation-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #cbcbcb;
    margin: 0.5rem;

    transition: background-color 0.2s ease-in-out;
  }

        .app__navigation-dot:hover {
      background-color: var(--secondary-color);
    }

    @media screen and (min-width: 2000px) {
      .app__navigation-dot{
        width: 20px;
        height: 20px;
    }
  }


@media screen and (max-width: 500px) {

  .app__navigation,
  .app__social {
    display: none;
  }

}

.head-text {
  font-size: 2rem;
}
  @media screen and (min-width: 768px) {
    .head-text{
      font-size: 2.2rem;
  }
}


